/* Styles for screens with a width of at least 360px (typical for small Android phones) */
@media screen and (min-width: 360px) {
  .your-element {
    min-width: 200px;
    /* Minimum width for the element on Android phones */
    max-width: 400px;
    /* Maximum width for the element on Android phones */
    font-size: 16px;
    /* Same font size for Android phones */
  }
}

/* Styles for screens with a width of at least 768px (typical for larger Android devices or tablets) */
@media screen and (min-width: 768px) {
  .your-element {
    min-width: 300px;
    /* Minimum width for the element on larger Android devices */
    max-width: 600px;
    /* Maximum width for the element on larger Android devices */
    font-size: 16px;
    /* Same font size for Android phones */
  }
}

