.leaf {
  width: 20px;
  height: 100px;
  background-color: black;
  position: absolute;
  transform-origin: bottom;
  top: 0;
  left: 50%;
  margin-left: -10px;
  border-radius: 50px 50px 0 0;
}

.leaf:nth-child(2) {
  transform: rotate(90deg);
}

.leaf:nth-child(3) {
  transform: rotate(180deg);
}

.leaf:nth-child(4) {
  transform: rotate(270deg);
}

