.container1 {
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%);
  height: 100vh;
  position: relative;
  overflow: hidden;
  text-align: center;
  transition: all 2s ease;
}

.text1 {
  font-size: 24px;
  position: absolute;
  bottom: 20px;
  left: 0;
  right: 0;
}

.content1 {
  padding: 20px;
  font-size: 18px;
  text-align: justify;
  opacity: 0;
  transition: all 2s ease;
}

.scrolled1 .text1 {
