.typearea {
  margin-top: 10%;
  height: 100vh;
}

.typearea h1::before {
  transform: scaleX(0);
  transform-origin: bottom right;
}

.typearea h1:hover::before {
  transform: scaleX(1);
  transform-origin: bottom left;
  background: hsl(0, 100%, 90%);
}

.typearea h1::before {
  content: " ";
  display: block;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  inset: 0 0 0 0;
  z-index: -1;
  transition: transform .6s ease;
  border-radius: 10px;
}

.typearea h1 {
  position: relative;
  font-size: 2rem;
}


